/*Parallax Slider (Main)
------------------------------------*/

.slider-inner .da-slider {
  box-shadow: none;
  border-bottom: 2px solid #ddd;
}

.slider-inner .da-dots {
  z-index: 10;
}

@media (max-width: 450px) {
  .da-slide p {
    display: none;
  }
}

@media (max-width: 900px) {
  /*Slider Parallax*/

  .da-slide .da-img {
    display: none;
  }
}

/*Revolution Slider
------------------------------------*/

.tp-dottedoverlay {
  z-index: 1 !important;
}

.tp-bullets.preview4 .bullet {
  border-radius: 10px !important;
}

.tp-banner-container a.btn-u,
.tp-banner-container a.btn-u:hover {
  color: #fff;
  border: solid 1px transparent;
}

.tp-banner-container a.re-btn-brd {
  background: none;
  border: solid 1px #fff;
}

.tp-banner-container a.re-btn-brd:hover {
  color: #555;
  background: #fff;
}

/*Fullwidth*/

.tp-banner-container .btn-u.btn-brd {
  font-weight: 400;
}

.tp-banner-container {
  position: relative;
}

/*Fullscreen*/

.fullscreen-container {
  padding: 0;
  width: 100%;
  position: relative;
}

/*Slider Contents v1*/

.revolution-mch-1:after {
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  content: ' ';
  position: absolute;
  background: rgba(0,0,0,0.3);
}

.revolution-ch1 {
  color: #fff;
  font-size: 40px;
  font-weight: 400;
  line-height: 60px;
  text-transform: uppercase;
  font-family: "Open Sans", Arial, sans-serif;
}

.revolution-ch2 {
  color: #fff;
  font-size: 20px;
  max-width: 470px;
  line-height: 32px;
  position: relative;
  text-align: center;
  font-weight: normal;
  font-family: "Open Sans", Arial, sans-serif;
}

.revolution-ch2:after {
  left: 50%;
  width: 70px;
  height: 2px;
  content: ' ';
  bottom: -20px;
  background: #fff;
  margin-left: -35px;
  position: absolute;
}

/*Slider Contents v2*/

.re-title-v1 {
  color: #fff;
  font-size: 75px;
  font-weight: 600;
  line-height: 60px;
  text-transform: uppercase;
  font-family: "Open Sans", Arial, sans-serif;
}

.re-title-v2 {
  color: #fff;
  font-size: 40px;
  font-weight: 600;
  line-height: 45px;
  text-align: center;
  text-transform: uppercase;
  font-family: "Open Sans", Arial, sans-serif;
}

.re-text-v1 {
  color: #fff;
  font-size: 24px;
  text-align: center;
  font-family: "Open Sans", Arial, sans-serif;
}

.re-text-v2 {
  color: #fff;
  font-size: 24px;
  font-family: "Open Sans", Arial, sans-serif;
}

/*Slides*/

.rs-caption-1 {
  color: #fff;
  font-size: 32px;
  font-weight: 200;
  padding: 6px 75px;
  position: relative;
  border-top: solid 1px #fff;
  border-bottom: solid 1px #fff;
}

.rs-caption-1:after,
.rs-caption-1:before {
  font-size: 16px;
  content: "\f005";
  position: absolute;
  font-family: FontAwesome;
}

@media (max-width: 768px) {
  .rs-caption-1:after,
  .rs-caption-1:before {
    content: "";
  }
}

.rs-caption-1:after {
  right: 4%;
}

.rs-caption-1:before {
  left: 4%;
}

.rs-caption-2 {
  color: #fff;
  position: relative;
  font-size: 26px;
  font-weight: 200;
  line-height: 40px;
  padding: 10px 50px;
  text-align: center;
}

.rs-caption-2:after {
  left: 50%;
  content: "";
  height: 1px;
  width: 100px;
  bottom: -10px;
  background: #fff;
  margin-left: -50px;
  position: absolute;
}

.rs-caption-3 a {
  margin: 0 5px;
  padding: 7px 20px;
  text-transform: uppercase;
}

@media (max-width: 768px) {
  .rs-caption-1,
  .rs-caption-2,
  .rs-caption-3 {
    font-weight: normal;
  }
}

/*Layer Slider
------------------------------------*/

.layer_slider {
  overflow: hidden;
}

.ls-ct-half,
.ls-ct-center {
  border-radius: 100px !important;
}

/*Sequence Slider (Home Page)
------------------------------------*/

@media (max-width: 900px) {
  #sequence-theme .info {
    top: 80px !important;
    width: 60% !important;
    margin-right: 50px;
  }
}

@media (max-width: 450px) {
  #sequence-theme .info {
    top: 70px !important;
    width: 70% !important;
    margin-right: 25px;
  }

  #sequence-theme h2 {
    font-size: 30px !important;
  }

  #sequence-theme .info p {
    font-size: 18px !important;
  }
}

/*Clients Carousel
------------------------------------*/

.clients {
  border: 0;
  overflow: hidden;
  margin-top: 10px;
  margin-bottom: 30px;
  box-shadow: none !important;
  margin-bottom: 0 !important;
}

.clients li {
  overflow: hidden;
  margin-left: 1px;
  position: relative;
  background: #fafafa;
}

.clients li:hover {
  background: #f6f6f6;
}

.clients li img.color-img {
  top: 0;
  left: 0;
  display: none;
  position: absolute;
}

.clients li:hover img.color-img {
  display: block;
}

.clients li img {
  filter: gray;
  /* IE6-9 */
  float: none !important;
  margin: 0 auto !important;
}

.clients li img:hover {
  filter: none;
}

/*Pie Chart
------------------------------------*/

.pie-progress-charts .inner-pchart {
  text-align: center;
}

.pie-progress-charts .circle {
  color: #555;
  font-weight: 200;
  margin-bottom: 20px;
}

.pie-progress-charts .circle-title {
  font-size: 22px;
}

@media (max-width: 767px) {
  .pie-progress-charts .inner-pchart {
    margin-bottom: 40px;
  }
}

/*Counter
------------------------------------*/

.counters {
  color: #555;
  text-align: center;
}

@media (max-width: 767px) {
  .counters {
    margin-bottom: 30px;
  }

  .counters:last-child {
    margin-bottom: inherit;
  }
}

.counters span {
  font-size: 35px;
}

.counters span.counter-icon {
  display: block;
  margin-bottom: 10px;
}

.counters span.counter-icon i {
  color: #fff;
  padding: 16px;
  min-width: 65px;
  font-size: 30px;
  position: relative;
  background: #72c02c;
}

.counters span.counter-icon i:after {
  width: 0;
  height: 0;
  left: 38%;
  content: " ";
  bottom: -7px;
  display: block;
  position: absolute;
  border-top: 7px solid #72c02c;
  border-left: 7px solid transparent;
  border-right: 7px solid transparent;
  border-left-style: inset;
  /*FF fixes*/
  border-right-style: inset;
  /*FF fixes*/
}

.counters:hover span.counter-icon i {
  border-color: #000;
}

/*Back To Top
------------------------------------*/

#topcontrol {
  color: #fff;
  z-index: 99;
  width: 30px;
  height: 30px;
  font-size: 20px;
  background: #222;
  position: relative;
  right: 14px !important;
  bottom: 11px !important;
  border-radius: 3px !important;
}

#topcontrol:after {
  top: -2px;
  left: 8.5px;
  content: "\f106";
  position: absolute;
  text-align: center;
  font-family: FontAwesome;
}

#topcontrol:hover {
  color: #fff;
  background: #72c02c;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

